.table-item {
  display: flex;
  padding: 24px 31px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #DFE0EB;
  &:hover {
    background-color: rgba(55, 81, 255, 0.04);
  }
  .user-info {
    width: 30%;
    h5 {
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.2px;
      color: #252733;
    }
    h6 {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.1px;
      color: #C5C7CD;
    }
  }
  .item-left {
    display: flex;
    align-items: center;
    width: 40%;
    .user-img {
      margin-right: 24px;
    }
  }
  .item-right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60%;
    .status-tag {
      font-style: normal;
      font-weight: bold;
      text-align: left;
      font-size: 11px;
      line-height: 14px;
      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      color: #FFFFFF;
      padding: 5px 12px;
      background: grey;
      border-radius: 100px;
      &.high {
        background-color: #F12B2C;
      }
      &.low {
        background-color: #FEC400;
      }
      &.normal {
        background-color: #29CC97;
      }
    }

    .action-button {
      border: none;
      background-color: transparent;
      width: 20%;
    }
  }
}