.sidebar-btn {
  display: flex;
  border: none;
  background-color: transparent;
  align-items: center;
  outline: none;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #A4A6B3;
  padding: 18px 24px;
  width: 100%;
  border-left: 3px solid transparent;
  transition: all ease 0.5s;
  .btn-icon {
    margin-right: 24px;
    svg {
      display: block;
      fill: rgba(159, 162, 180, 0.4);
    }
  }
  &:hover, &.active {
    background: rgba(159, 162, 180, 0.08);
    border-left: 3px solid #DDE2FF;
    color: #DDE2FF;
    .btn-icon {
      svg {
        fill: #DDE2FF;
      }
    }
  }
}