.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 50px;
  &-left {
    .page-title {
      font-style: normal;
      font-weight: bold;
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 0.3px;
      text-transform: capitalize;
      color: #252733;
    }
  }
  &-right {
    display: flex;
    align-items: center;
    .icon-btn {
      display: block;
      border: none;
      outline: none;
      background-color: transparent;
      padding: 12px;
    }

    .user-account-dropdown {
      border-left: 1px solid #DFE0EB;
      padding-left: 32px;
      margin-left: 32px;
      display: flex;
      align-items: center;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      text-align: right;
      letter-spacing: 0.2px;
      color: #252733;
      text-decoration: none;
      img {
        margin-left: 14px;
      }
    }
  }
}