.sidebar {
  height: 100vh;
  padding: 30px 0;
  box-sizing: border-box;
  background-color: #363740;
  .sidebar-logo {
    padding: 18px 24px;
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 0.4px;
    color: rgba(164, 166, 179, 0.7);
    text-decoration: none;
    img {
      margin-right: 12px;
    }
  }
  .sidebar-link {
    text-decoration: none;
  }
}